<!-- 健康体检报告 -->
<template>
  <div class="clinic-detail-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel="2"></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handlePrint()">
          <cc-svg-icon icon-class="print"></cc-svg-icon>
          <span>打印报告</span>
        </div>
      </div>
    </div>
    <!-- 体检结果 -->
    <div class="inner-box" ref="reportInfo">
      <div class="report-result-box">
        <div class="title">
          <img src="../../assets/images/hospital.png" />
          健康体检报告
        </div>
        <!-- 基本信息 -->
        <div class="text-box baseInfo-box">
          <h3>基本信息</h3>
          <ul>
            <li>
              <span>姓名：</span><label>{{ reportInfo.userName || EMPTYTEXT }}</label>
            </li>
            <li>
              <span>性别：</span><label>{{ reportInfo.sexTypeStr || EMPTYTEXT }}</label>
            </li>
            <li>
              <span>年龄：</span>
              <label>{{ reportInfo.age !== null ? reportInfo.age + '岁' : EMPTYTEXT }}</label>
            </li>
            <li>
              <span>身份证号：</span><label>{{ reportInfo.idNo || EMPTYTEXT }}</label>
            </li>
            <li>
              <span>手机号：</span><label>{{ reportInfo.mobile || EMPTYTEXT }}</label>
            </li>
            <li>
              <span>体检日期：</span
              ><label>{{
                reportInfo.createDateStr ? reportInfo.createDateStr.substr(0, 10) : EMPTYTEXT
              }}</label>
            </li>
          </ul>
        </div>
        <!-- 体检项目 -->
        <div class="medical-items-box">
          <div class="item-box table-box" v-for="(item, index) in resultList" :key="index">
            <h3>{{ item.projectName }}</h3>
            <table cellspacing="0" cellspadding="0" class="resident-box">
              <thead>
                <tr>
                  <th>项目名称</th>
                  <th>检查结果</th>
                  <th>提示</th>
                  <th>单位</th>
                  <th>参考范围</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(it, i) in item.childrens" :key="i">
                  <td>{{ it.itemName || EMPTYTEXT }}</td>
                  <td>{{ it.resultDesc || EMPTYTEXT }}</td>
                  <td>{{ it.resultTip || EMPTYTEXT }}</td>
                  <td>{{ it.calcUnit || EMPTYTEXT }}</td>
                  <td v-html="it.refDesc || EMPTYTEXT"></td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- 诊断结果 -->
        <div class="text-box result-box">
          <h3>门诊诊断</h3>
          <ul>
            <li>
              <span>诊断意见：</span><label>{{ reportInfo.diagnoseOpinion || EMPTYTEXT }}</label>
            </li>
          </ul>
        </div>
        <div class="sign-box">
          <p>
            医生签名：<span>{{ reportInfo.registerUser || EMPTYTEXT }}</span>
          </p>
          <P
            >日期：<span>{{
              reportInfo.createDateStr ? reportInfo.createDateStr.substr(0, 10) : EMPTYTEXT
            }}</span></P
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import printMethod from '../../util/printMethod' //打印方法
export default {
  name: 'clinic-detail',
  data() {
    return {
      reportInfo: {}, //报告结果
      resultList: [] //整合数据集
    }
  },
  created() {
    this.getDetailInfo() //获取报告信息
  },
  methods: {
    //获取报告信息
    getDetailInfo() {
      this.$emit('viewLoad', true)
      this.$http
        .post(process.env.VUE_APP_API + '/family/examResult/getDtoByResultIdList', {
          resultIdList: this.$route.query.id
        })
        .then(res => {
          if (res.code === 200) {
            this.reportInfo = res.data[0] || {}
            //特殊数据处理
            this.resultList = []
            this.reportInfo.items.forEach(item => {
              if (this.resultList.length > 0) {
                let result = this.resultList.find(it => it.projectId == item.projectId)
                if (result) {
                  let obj = {}
                  obj.itemName = item.itemName //小项目名称
                  obj.refDesc = item.refDesc // 小项目范围
                  obj.resultDesc = item.resultDesc //小项目结果
                  obj.calcUnit = item.calcUnit //小项目单位
                  obj.resultTip = item.resultTip //提示
                  result.childrens.push(obj)
                } else {
                  let parentObj = {}
                  parentObj.projectId = item.projectId //大项目id
                  parentObj.projectName = item.projectName //大项目名称
                  parentObj.childrens = []
                  let obj = {}
                  obj.itemName = item.itemName //小项目名称
                  obj.refDesc = item.refDesc // 小项目范围
                  obj.resultDesc = item.resultDesc //小项目结果
                  obj.calcUnit = item.calcUnit //小项目单位
                  obj.resultTip = item.resultTip //提示
                  parentObj.childrens.push(obj)
                  this.resultList.push(parentObj)
                }
              } else {
                let parentObj = {}
                parentObj.projectId = item.projectId
                parentObj.projectName = item.projectName
                parentObj.childrens = []
                let obj = {}
                obj.itemName = item.itemName //小项目名称
                obj.refDesc = item.refDesc // 小项目范围
                obj.resultDesc = item.resultDesc //小项目结果
                obj.calcUnit = item.calcUnit //小项目单位
                obj.resultTip = item.resultTip //提示
                parentObj.childrens.push(obj)
                this.resultList.push(parentObj)
              }
            })
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    },
    //打印报告
    handlePrint() {
      this.$confirm('确定要打印该报告吗？', '提示', {
        confirmButtonText: '确 认',
        cancelButtonText: '取 消',
        customClass: 'infoMessage',
        center: true
      }).then(() => {
        printMethod(this.$refs['reportInfo'], '健康体检报告')
        let loading = this.$loading()
        setTimeout(() => {
          loading.close()
        }, 800)
      })
    }
  }
}
</script>

<style lang="less" scoped>
@media print {
  .report-result-box {
    .text-box {
      ul {
        li {
          width: 33.33%;
          padding-right: 0px;
        }
      }
    }
  }
}
.clinic-detail-box {
  .inner-box {
    width: 80%;
    margin: 70px auto;
  }
}
.report-result-box {
  height: 2000px;
  .title {
    font-size: 48px;
    color: #49c38a;
    text-align: center;
    margin-bottom: 30px;
    .img {
      width: 69px;
      height: 71px;
    }
  }
  h3 {
    font-size: 16px;
    color: #62523a;
    margin: 20px 0px;
    font-weight: bold;
  }
  .text-box {
    ul {
      display: flex;
      flex-wrap: wrap;
      > li {
        min-width: 25%;
        padding-right: 3%;
        box-sizing: border-box;
        line-height: 20px;
        list-style: none;
        margin-bottom: 20px;
        font-size: 14px;
        color: #746651;
        display: flex;
        span {
          text-align: right;
        }
        label {
          display: inline-block;
          flex: 1;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }
      }
    }
    &.result-box {
      li {
        width: 100%;
      }
    }
  }
  .medical-items-box {
    .table-box {
      table {
        width: 100%;
        border-right: 1px solid #f4ebe3;
        border-top: 1px solid #f4ebe3;
        margin: 15px 0px 30px;
        table-layout: fixed;
        th,
        td {
          border-left: 1px solid #f4ebe3;
          border-bottom: 1px solid #f4ebe3;
          text-align: center;
        }
        th {
          background-color: #fcf7ed;
          padding: 0px !important;
          height: 40px;
        }
        tbody {
          td {
            line-height: 35px;
            height: 35px;
            padding: 0px !important;
          }
        }
      }
    }
  }
  .sign-box {
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    p {
      font-size: 18px;
      margin-bottom: 15px;
      width: 160px;
    }
  }
}
</style>
